/**
 * SEO component that queries for data with
 * Gatsby's useStaticQuery React hook
 *
 * See: https://www.gatsbyjs.com/docs/how-to/querying-data/use-static-query/
 */

import * as React from "react"
import { useStaticQuery, graphql } from "gatsby"
import { Helmet } from "react-helmet";

const joinPaths = (root, suffix) => (
  (root.endsWith("/") ? root.slice(0, root.length - 1) : root)
  + "/" +
  (suffix.startsWith("/") ? suffix.slice(1) : suffix)
)

function Seo({ description, title, children, path, image, keywords }) {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
            author
            siteUrl
            globalKeywords
            defaultKeywords
            image {
              src
              width
              height
            }
          }
        }
      }
    `
  )

  const metaDescription = description || site.siteMetadata.description
  const metaTitle = title ? `${title} | ${site.siteMetadata.title}` : site.siteMetadata.title;
  const metaImage = image || site.siteMetadata.image;
  const metaImageSrc = joinPaths(site.siteMetadata.siteUrl, metaImage.src);
  const metaUrl = path ? joinPaths(site.siteMetadata.siteUrl, path) : site.siteMetadata.siteUrl;
  const metaKeywords = [...(keywords || site.siteMetadata.defaultKeywords), ...site.siteMetadata.globalKeywords];

  return (
    <Helmet>
      <title>{metaTitle}</title>

      <meta name="description" content={metaDescription} />
      <meta name="keywords" content={metaKeywords.join(", ")} />

      <meta property="og:title" content={metaTitle} />
      <meta property="og:url" content={metaUrl} />
      <meta property="og:description" content={metaDescription} />
      <meta property="og:type" content="website" />
      <meta property="og:image" content={metaImageSrc} />
      {metaImage.width && (<meta property="og:image:width" content={metaImage.width} />)}
      {metaImage.height && (<meta property="og:image:height" content={metaImage.height} />)}

      <meta name="twitter:card" content="summary" />
      <meta name="twitter:creator" content={site.siteMetadata.author || ``} />
      <meta name="twitter:title" content={metaTitle} />
      <meta name="twitter:description" content={metaDescription} />
      <meta name="twitter:image" content={metaImageSrc} />
      {metaImage.width && (<meta name="twitter:image:width" content={metaImage.width} />)}
      {metaImage.height && (<meta name="twitter:image:height" content={metaImage.height} />)}

      {children}
    </Helmet>
  )
}

export default Seo
